.swiper2-button-prev,
.swiper2-button-next {
  position: static;
  margin-left: 13px;
}

.swiper2-button-prev::after,
.swiper2-button-next::after {
  display: none;
}

.swiper2-button-next,
.swiper2-button-prev {
  position: relative;
  width: 32px;
  height: 32px;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  border: 2px solid #a65cf0;
  border-radius: 50%;
  background: none;
  z-index: 5;
  transition: background 0.3s ease-in-out, border-color 0.3s ease-in-out;
}

.swiper2-button-next:focus,
.swiper2-button-prev:focus {
  background: #c998fb;
}

.swiper2-button-next:hover,
.swiper2-button-prev:hover {
  background: #a65cf0;
}

.swiper2-button-next:active,
.swiper2-button-prev:active {
  background: #7033ac;
}

.swiper2-button-prev.swiper-button-disabled,
.swiper2-button-next.swiper-button-disabled {
  opacity: 1;
  border-color: #999;
}

.swiper2-button-prev.swiper-button-disabled::before,
.swiper2-button-next.swiper-button-disabled::before {
  opacity: 1;
  border-color: #999;
}

.swiper2-button-next::before {
  content: "";
  position: absolute;
  width: 8px;
  height: 8px;
  top: 35%;
  bottom: 50%;
  right: 50%;
  left: 33%;
  border-bottom: 2px solid #a65cf0;
  border-right: 2px solid #a65cf0;
  transform: rotate(-45deg);
  z-index: 5;
  transition: border-color 0.3s ease-in-out;
}

.swiper2-button-prev::before {
  content: "";
  position: absolute;
  width: 8px;
  height: 8px;
  top: 35%;
  bottom: 50%;
  right: 50%;
  left: 39%;
  border-top: 2px solid #a65cf0;
  border-left: 2px solid #a65cf0;
  transform: rotate(-45deg);
  z-index: 5;
  transition: border-color 0.3s ease-in-out;
}

.swiper2-button-prev:focus::before,
.swiper2-button-next:focus::before {
  border-color: #fff;
}

.swiper2-button-prev:hover::before,
.swiper2-button-next:hover::before {
  border-color: #fff;
}

.swiper2-button-prev:active::before,
.swiper2-button-next:active::before {
  border-color: #fff;
}

